import Grid from "@mui/material/Unstable_Grid2";
import {
    Box,
    Button,
    TextField,
    Paper,
} from "@mui/material";

function AddComment({ handleSubmitCommentForm }) {
    return (
        <Paper>
            <Box
                component={"form"}
                onSubmit={handleSubmitCommentForm}
                sx={{ mt: 3 }}
            >
                <Grid container spacing={2}>
                    <Grid xs={12} md={6}>
                        <TextField
                            label="   "
                            variant="outlined"
                            name="fullname"
                            type={"text"}
                            required
                            helperText="     ."
                            fullWidth
                        />
                    </Grid>
                    <Grid xs={12} md={6}>
                        <TextField
                            label=""
                            required
                            helperText="   ."
                            name="email"
                            type={"email"}
                            variant="outlined"
                            fullWidth
                        />
                    </Grid>
                    <Grid xs={12}>
                        <TextField
                            name="message"
                            label=" "
                            variant="outlined"
                            multiline
                            required
                            rows={3}
                            fullWidth
                        />
                    </Grid>
                    <Button variant="contained" type="submit" fullWidth sx={{ mx: 1 }}>
                         
                    </Button>
                </Grid>
            </Box>
        </Paper>
    )
}

export default AddComment